html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  overflow-y: scroll;
  font-family: "Arimo", sans-serif;
  font-weight: 400;
  color: var(--Color-black);
  background-color: var(--WhiteGreen);
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 140px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

@media (max-width: 800px) {
  .container {
    margin: 0 auto;
    padding: 0 0px;
  }
}

.header {
  width: 100%;
  left: 0;
  top: 0;
}

.header__container {
  height: 70px;
  padding-top: 28px;
  padding-bottom: 24px;
  padding-right: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 9999999999;
}

.header__logo {
  margin-bottom: 33px;
}

.burger {
  position: absolute;
  display: none;
  cursor: pointer;
  margin-top: -18px;
  right: 0;
  width: 100px;
  height: 150px;
  z-index: 999999999;
}

.burger::before {
  content: "";
  position: absolute;
  top: 50%;
  width: 26px;
  height: 2px;
  background-color: var(--Color-black);
}

.burger::after {
  content: "";
  position: absolute;
  top: 60.5%;
  width: 26px;
  height: 2px;
  background-color: var(--Color-black);
}

.burger__line {
  position: absolute;
  top: 55.7%;
  transform: translateY(-50%);
  width: 26px;
  height: 2px;
  background-color: var(--Color-black);
}

.burger__logo {
  position: absolute;
  top: -30px;
  width: 15vw;
  fill: var(--Color-white);
}

@media (max-width: 800px) {
  .burger {
    display: flex;
    align-items: center;
    right: -40px;
    margin-top: -5px;
  }
}

.nav__container {
  position: relative;
  display: flex;
  align-items: center;
}

.nav {
  z-index: 99999999;
}

.nav__list {
  display: flex;
  margin-top: 3px;
  margin-right: 193px;
}

.nav__close {
  display: none;
}

.nav__item {
  z-index: 999999;
}

.nav__item:not(:last-child) {
  margin-right: 68px;
}

.nav__mobile--buttonWrap {
  display: none;
}

.nav__link {
  position: relative;
  font-size: 14.5px;
  font-weight: 400;
}

.nav__link:hover {
  color: var(--TulipTree);
  transition: 0.15s;
}

.nav__link--current {
  color: var(--TulipTree);
  transition: 0.15s;
}

.nav__phone {
  position: absolute;
  font-size: 28px;
  font-weight: 400;
  right: 124px;
  margin-top: -7px;
}

@media (max-width: 1200px) {
  .nav__phone {
    right: 30px;
  }
}

@media (max-width: 1060px) {
  .nav__phone {
    display: none;
  }
  .nav__container {
    justify-content: right;
  }
  .nav__list {
    margin-right: 30px;
  }
  .nav__link {
    padding-left: 35px;
  }
}

@media (max-width: 1000px) {
  .nav .nav__phone {
    display: none;
  }
  .nav .nav__container {
    justify-content: right;
  }
  .nav .nav__list {
    margin-right: 30px;
  }
  .nav .nav__link {
    padding-left: 20px;
  }
}

@media (max-width: 900px) {
  .nav .nav__phone {
    display: none;
  }
  .nav .nav__container {
    justify-content: right;
  }
  .nav .nav__list {
    margin-right: 5px;
  }
  .nav .nav__link {
    padding-left: 5px;
  }
}

@media (max-width: 800px) {
  .nav {
    height: 500vw;
    justify-content: left;
    align-items: center;
    left: 0;
    top: 0;
    width: 100%;
    padding: 10%;
    background-color: var(--color-dark);
    left: -150vw;
    position: fixed;
    z-index: 99999999900;
    transition: left 0.3s;
  }
  .nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .nav__list {
    padding-top: 100px;
    padding-left: 60px;
    display: block;
    text-align: center;
  }
  .nav__link {
    font-size: 4vw;
  }
  .nav__link:hover {
    color: var(--Color-white);
  }
  .nav__link--current {
    color: var(--Color-white);
  }
  .nav__close {
    position: absolute;
    display: block;
    right: 70px;
    margin-top: 200px;
    top: 0;
    color: var(--Color-white);
  }
  .nav--visible {
    left: 0;
    transform: left 0.3s;
  }
  .nav__item {
    color: var(--Color-white);
  }
  .nav__item:not(:last-child) {
    padding-bottom: 32px;
  }
  .nav__closeing {
    position: absolute;
    top: -230px;
    width: 3vw;
  }
  .nav__mobile--buttonWrap {
    display: block;
    width: 320px;
    height: 60px;
    padding-top: 20px;
    margin-top: 60px;
    cursor: pointer;
    margin: 60px  auto;
    text-align: center;
    background-color: var(--Color-white);
    box-shadow: 0px 20px 40px rgba(255, 255, 255, 0.08);
    border-radius: 10px;
  }
  .nav__mobile--buttonWrap button {
    color: var(--Color-black);
  }
}

@media (max-width: 415px) {
  .nav {
    height: 500vw;
    justify-content: left;
    left: 0;
    top: 0;
    width: 100%;
    padding: 10%;
    background-color: var(--color-dark);
    left: -150vw;
    position: fixed;
