    transition: left 0.3s;
  }
  .nav__list {
    padding-top: 100px;
    padding-left: 60px;
    display: block;
    text-align: center;
  }
  .nav__link {
    font-size: 4vw;
  }
  .nav__link:hover {
    color: var(--Color-white);
  }
  .nav__link--current {
    color: var(--Color-white);
  }
  .nav__close {
    position: absolute;
    display: block;
    right: 40px;
    margin-top: 200px;
    top: 0;
    color: var(--Color-white);
  }
  .nav--visible {
    left: 0;
    transform: left 0.3s;
  }
  .nav__item {
    color: var(--Color-white);
  }
  .nav__item:not(:last-child) {
    padding-bottom: 32px;
  }
  .nav__closeing {
    position: absolute;
    top: -230px;
    width: 3vw;
  }
  .nav__mobile--buttonWrap {
    display: block;
    width: 260px;
    height: 45px;
    padding-top: 17px;
    margin-top: 60px;
    cursor: pointer;
    margin: 60px  auto;
    font-size: 13px;
    text-align: center;
    background-color: var(--Color-white);
    box-shadow: 0px 20px 40px rgba(255, 255, 255, 0.08);
    border-radius: 10px;
  }
}

.logo {
  position: absolute;
  display: inline-block;
  width: 103px;
  height: 22px;
  left: 19px;
  margin-top: 25px;
