:root {
  --level-one: translateZ(6rem);
  --level-two: translateZ(6rem);
  --level-three: translateZ(6rem);
  --level-swg: translateZ(6rem);
  --fw-normal: 400;
  --fw-bold: 700;
  --clr: #b7c9e5;
}

.card {
  position: relative;
  width: 215px;
  height: 300px;
  cursor: pointer;
}

.card__wr {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99999999999999;
}

.card__content {
  text-align: center;
  position: relative;
  transition: transform 2s;
  transform-style: preserve-3d;
}

.card__front {
  width: 215px;
  height: 300px;
  background-color: var(--Color-white);
  background-size: cover;
  background-blend-mode: overlay;
  color: var(--MineShaft);
  box-shadow: 4px 5px 15px rgba(111, 101, 110, 0.1);
  border-radius: 10px;
  z-index: 999999999;
}

.card__front svg {
  margin-top: -170px;
  margin-left: -18px;
  width: 40%;
}

.card__back {
  width: 215px;
  height: 300px;
  transform: rotateY(0.5turn);
  color: var(--Color-black);
  background-color: var(--TulipTree);
  box-shadow: 4px 5px 15px rgba(111, 101, 110, 0.1);
  border-radius: 10px;
}

.card__title {
  position: absolute;
  width: 100px;
  bottom: 10px;
  padding-left: 40px;
  font-weight: 600;
  transform: var(--level-three);
  order: 2;
  font-size: 14px;
  line-height: 130%;
  text-transform: uppercase;
  text-align: left;
}

.card__body {
  transform: var(--level-two);
  font-size: 20px;
  line-height: 1.6;
}

.card__body a::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.card:hover .card__content {
  margin-top: -15px;
  transform: rotateY(0.5turn);
}

.card__front,
.card__back {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5em 3em;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  display: grid;
  align-content: center;
  z-index: 999999;
}

#targeting {
  margin-left: -5px;
}

@media screen and (max-width: 1080px) {
  .card:hover .card__content {
    margin-top: 0px;
    transform: rotateY(0turn);
  }
  .card:hover .card__content .card__title {
    color: var(--Color-white);
  }
  .card:hover .card__front {
    transition: 1s;
    background-color: var(--TulipTree);
  }
  .card__back {
    display: none;
  }
}

@media (max-width: 1024px) {
  .card {
    position: relative;
    width: 205px;
    height: 280px;
    cursor: pointer;
  }
}

